<template>
  <div>
    <!-- <img src="@/assets/images/my/greenpower_bg@2x.png" alt="" style="width:100%;"> -->
    <div id="container"></div>
    <img src="@/assets/images/my/img_banner@2x.png" alt="" class="pos" @click="$router.push({path:'/reg'})" v-if="!mobile">
    <img src="@/assets/images/my/11.gif" alt="" class="pos" @click="$router.push({path:'/ruzhu'})" v-else>
    <div class="notice">
      <van-notice-bar
        :text="system_mess"
        left-icon="volume-o"
      />
    </div>
    <div @click="saoma()" class="saoma">
      <img src="@/assets/images/my/ic_scan@2x.png" alt="" class="saoma_icon">
      扫码充电</div>

    <navbar :active=0></navbar>
  </div>
</template>

<script charset="utf-8" src="https://map.qq.com/api/js?v=2.exp&key=XN7BZ-YERK4-R4IUK-DRWL3-ENSG6-NBFE6"></script>
<script>
import navbar from '@/components/index.vue'
import { getsaoma, getjwd } from '@/axios/getData';
import { NoticeBar } from 'vant'
export default {
  data(){
    return{
      jd:'',
      wd:"",
      jiudian_list:[],    //  酒店列表   经纬度
      mobile:'',          //  判断是否注册过 
      system_mess:"",   // 公告
    }
  },
  components: {
    navbar,
    vanNoticeBar: NoticeBar
  },
  created() {
    var url = window.location.href
    if(url.search('openid=')!=-1){
      var openid = url.split('openid=')[1];
      localStorage.setItem('openid',openid)
    }
    this.getdata();
    this.getjiudian();
  },
  methods:{
    init(jwd_list) {
      var list = JSON.parse(jwd_list)
      var that = this
      var id = document.getElementById('container');
      console.log(id)
      //  中心点位置
      setTimeout(()=>{

        var center = new qq.maps.LatLng(that.wd,that.jd);
        var map = new qq.maps.Map(id,{
            mapTypeControlOptions: {
              mapTypeIds: [],
            },
            zoomControl: false,   //  禁止缩放
            center: center,
            zoom: 15
        });
        

        var circle = new qq.maps.Circle({
          center: center,
          map: map,
          radius: 20
        });
        

        var marker;
        list.forEach(item => {
          var marker_pos = new qq.maps.LatLng(Number(item.wd), Number(item.jd));
          marker = new qq.maps.Marker({
              position: marker_pos,
              map: map,
          });
          var anchor = new qq.maps.Point(10.86956,28.57142),
              scaleSize = new qq.maps.Size(23,35),
                size = new qq.maps.Size(50, 100),
                origin = new qq.maps.Point(0,0),
              markerIcon = new qq.maps.MarkerImage(
            "http://gking008.com/public/upload/user/smcd_log.png",
            size,
            origin,
            anchor,
            scaleSize, 
          );
          marker.setIcon(markerIcon);
        });
      },2000)
    },
    saoma(){
      console.log(123)
      wx.ready(() => {
        wx.scanQRCode({
          needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
          desc: "scanQRCode desc",
          scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
          success: r => {
            // var str = JSON.parse(JSON.stringify(r));
            // alert(str)
          }
        });
      });
    },
    getdata(){
      var that = this
      getsaoma().then(res => {
        // console.log(res.data)
        let data = res.data;
        let op = {
          debug: false,
          appId: data.appId, //公众号的唯一标识
          timestamp: data.timestamp, // 生成签名的时间戳
          nonceStr: data.nonceStr, // 生成签名的随机串
          signature: data.signature, // 签名
          jsApiList: ["checkJsApi", "scanQRCode", "getLocation"] // 需要使用的JS接口列表(我只需要调用扫一扫的接口，如有多个接口用逗号分隔)
        };
        wx.config(op);
        // that.saoma();
        wx.ready(() => {
          wx.getLocation({
            type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
            success: function (res) {
              that.wd = res.latitude; // 纬度，浮点数，范围为90 ~ -90
              that.jd = res.longitude; // 经度，浮点数，范围为180 ~ -180。
              var speed = res.speed; // 速度，以米/每秒计
              var accuracy = res.accuracy; // 位置精度
              console.log(res,123)
            }
          });
        });
        wx.error(r => {

        });
      });
    },
    getjiudian(){
      var that = this
      getjwd().then((res)=>{
        var list = res.data.list
        this.mobile = res.data.mobile
        this.system_mess = res.data.system_mess
          setTimeout(()=>{
            console.log(123)
            that.init(JSON.stringify(list))
          },2000)
        console.log(res.data,5656,res)
      });
    },
  },
  mounted(){
    // this.init();
  }
}
</script>

<style scoped lang="scss">
/deep/ .van-loading{
  background: transparent !important;
}
.again{
  position: absolute;

}
#container{
	min-width:600px;
	height:100vh;
}
.saoma{
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom:8%;
  left:50%;
  transform: translate3d(-50%,-50%,0);
  width:80%;
  line-height: 32pt;
  background: #15BDDB;
  border-radius: 16pt;
  color:#fff;
  text-align: center;
  font-size: 13pt;
}
.saoma_icon{
  margin-right: 20px;
  width:45px;
  height:45px;
}
.pos{
  position: fixed;
  top:8%;
  width:80%;
  left:10%;
  z-index: 20;
}
.notice{
  position: fixed;
  top:0px;
  width:100%;
}
</style>



